"use client";

import { useMemo, useRef, useState } from "react";
import { useRouter } from "next/navigation";

type SummaryStyle = "bullet" | "explained";
type Difficulty = "easy" | "medium" | "hard";

type ApiOk = {
  success: true;
  data: any;
  meta?: {
    materialId?: string;
    usage?: {
      plan?: "FREE" | "PRO" | "ADMIN";
      monthlyUsed?: number | null;
      limit?: number | null;
    };
    options?: {
      flashcardsCount?: number;
      topicsCount?: number;
      summaryStyle?: SummaryStyle;
      flashcardDifficulty?: Difficulty;
    };
  };
};

type ApiErr = {
  success: false;
  error?: string;
  details?: string;
  code?: string;
  meta?: any;
};

type Props = {
  onDataProcessed?: (data: any) => void;
};

export default function UploadSection({ onDataProcessed }: Props) {
  const router = useRouter();
  const inputRef = useRef<HTMLInputElement | null>(null);

  const [isUploading, setIsUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // op√ß√µes (como voc√™ pediu)
  const [summaryStyle, setSummaryStyle] = useState<SummaryStyle>("explained");
  const [difficulty, setDifficulty] = useState<Difficulty>("medium");
  const [flashcardsCount, setFlashcardsCount] = useState<number>(10);

  // sucesso
  const [success, setSuccess] = useState(false);
  const [lastMaterialId, setLastMaterialId] = useState<string | null>(null);
  const [lastFilename, setLastFilename] = useState<string | null>(null);

  const flashcardsHint = useMemo(() => {
    if (difficulty === "easy") return "F√°cil (conceitos b√°sicos)";
    if (difficulty === "hard") return "Dif√≠cil (aplica√ß√£o e pegadinhas)";
    return "M√©dio (conceitos e rela√ß√µes)";
  }, [difficulty]);

  function onPickFile() {
    inputRef.current?.click();
  }

  async function onFileChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (!file) return;
    await handleUpload(file);
    e.target.value = "";
  }

  async function onDrop(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault();
    const file = e.dataTransfer.files?.[0];
    if (!file) return;
    await handleUpload(file);
  }

  function onDragOver(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault();
  }

  async function handleUpload(file: File) {
    setError(null);
    setSuccess(false);
    setIsUploading(true);
    setLastFilename(file.name);

    try {
      const formData = new FormData();
      formData.append("file", file);

      // op√ß√µes para o route.ts
      formData.append("summaryStyle", summaryStyle);
      formData.append("flashcardDifficulty", difficulty);
      formData.append("flashcardsCount", String(flashcardsCount));
      formData.append("topicsCount", "4");

      const response = await fetch("/api/process-study-material", {
        method: "POST",
        body: formData,
      });

      const json = (await response.json()) as ApiOk | ApiErr;

      if (!response.ok || !(json as any)?.success) {
        const j = json as ApiErr;

        if (response.status === 402 && j.code === "LIMIT_REACHED") {
          setError("Voc√™ atingiu o limite mensal do plano gr√°tis. Assine para continuar.");
          router.push("/pricing"); // se existir
          return;
        }

        throw new Error(j.details || j.error || "Falha ao processar arquivo");
      }

      const ok = json as ApiOk;

      // mant√©m compatibilidade com sua Home
      onDataProcessed?.(ok.data);

      // persist√™ncia local (ajuda, mas o DB √© a fonte real)
      const materialId = ok.meta?.materialId || null;
      setLastMaterialId(materialId);
      setSuccess(true);

      try {
        localStorage.setItem("studai:lastMaterialId:v1", materialId || "");
        localStorage.setItem("studai:lastMaterialData:v1", JSON.stringify(ok.data));
        localStorage.setItem("studai:lastMaterialFilename:v1", file.name);
      } catch {}
    } catch (e: any) {
      setError(e?.message || "Erro inesperado");
    } finally {
      setIsUploading(false);
    }
  }

  return (
    <section className="w-full">
      {/* op√ß√µes */}
      <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="rounded-2xl border border-[#1A1A1A] bg-[#0D0D0D] p-4">
          <div className="text-sm font-semibold text-white mb-2">Tipo de resumo</div>

          <div className="flex gap-2">
            <button
              type="button"
              onClick={() => setSummaryStyle("bullet")}
              className={`px-3 py-2 rounded-xl border text-sm transition
              ${
                summaryStyle === "bullet"
                  ? "border-[#00FF8B]/40 bg-[#00FF8B]/10 text-[#00FF8B]"
                  : "border-[#1A1A1A] text-gray-300 hover:bg-[#1A1A1A]"
              }`}
            >
              Bullet
            </button>

            <button
              type="button"
              onClick={() => setSummaryStyle("explained")}
              className={`px-3 py-2 rounded-xl border text-sm transition
              ${
                summaryStyle === "explained"
                  ? "border-[#00FF8B]/40 bg-[#00FF8B]/10 text-[#00FF8B]"
                  : "border-[#1A1A1A] text-gray-300 hover:bg-[#1A1A1A]"
              }`}
            >
              Explicado
            </button>
          </div>

          <div className="mt-2 text-xs text-gray-400">
            {summaryStyle === "bullet"
              ? "Resumo objetivo em t√≥picos."
              : "Resumo detalhado em texto explicado."}
          </div>
        </div>

        <div className="rounded-2xl border border-[#1A1A1A] bg-[#0D0D0D] p-4">
          <div className="text-sm font-semibold text-white mb-2">Dificuldade dos flashcards</div>

          <div className="flex gap-2 flex-wrap">
            {(["easy", "medium", "hard"] as Difficulty[]).map((d) => (
              <button
                key={d}
                type="button"
                onClick={() => setDifficulty(d)}
                className={`px-3 py-2 rounded-xl border text-sm transition
                ${
                  difficulty === d
                    ? "border-[#00FF8B]/40 bg-[#00FF8B]/10 text-[#00FF8B]"
                    : "border-[#1A1A1A] text-gray-300 hover:bg-[#1A1A1A]"
                }`}
              >
                {d === "easy" ? "F√°cil" : d === "medium" ? "M√©dio" : "Dif√≠cil"}
              </button>
            ))}
          </div>

          <div className="mt-2 text-xs text-gray-400">{flashcardsHint}</div>
        </div>

        <div className="rounded-2xl border border-[#1A1A1A] bg-[#0D0D0D] p-4">
          <div className="text-sm font-semibold text-white mb-2">Quantidade de flashcards</div>

          <div className="flex items-center gap-3">
            <input
              type="range"
              min={5}
              max={30}
              value={flashcardsCount}
              onChange={(e) => setFlashcardsCount(Number(e.target.value))}
              className="w-full accent-[#00FF8B]"
            />
            <div className="w-10 text-right text-sm text-gray-200">{flashcardsCount}</div>
          </div>

          <div className="mt-2 text-xs text-gray-400">Entre 5 e 30</div>
        </div>
      </div>

      {/* upload */}
      <input
        ref={inputRef}
        type="file"
        className="hidden"
        onChange={onFileChange}
        accept="image/*,application/pdf,text/plain"
      />

      <div
        onDrop={onDrop}
        onDragOver={onDragOver}
        className="w-full rounded-2xl border border-dashed border-[#00FF8B]/25 bg-[#0D0D0D] p-10 text-center"
      >
        <div className="text-lg font-semibold text-white">Arraste e solte seu arquivo aqui</div>
        <div className="mt-2 text-sm text-gray-400">ou clique para selecionar</div>

        <button
          type="button"
          onClick={onPickFile}
          disabled={isUploading}
          className="mt-6 inline-flex items-center justify-center rounded-xl border border-[#1A1A1A] px-5 py-2 text-sm text-gray-200 hover:bg-[#1A1A1A] disabled:opacity-60"
        >
          {isUploading ? "Processando..." : "Selecionar arquivo"}
        </button>

        {lastFilename ? (
          <div className="mt-4 text-xs text-gray-500">Arquivo: {lastFilename}</div>
        ) : null}

        {error ? <div className="mt-4 text-sm text-red-400">{error}</div> : null}

        {/* sucesso bonito */}
        {success ? (
          <div className="mt-6 w-full max-w-2xl mx-auto rounded-2xl border border-[#00FF8B]/25 bg-[#00FF8B]/10 p-5 text-left">
            <div className="flex items-start gap-3">
              <div className="mt-0.5 flex h-8 w-8 items-center justify-center rounded-xl bg-[#00FF8B]/15 border border-[#00FF8B]/25">
                <span className="text-[#00FF8B] text-lg leading-none">‚úÖ</span>
              </div>

              <div className="flex-1 min-w-0">
                <div className="text-base font-semibold text-[#00FF8B]">Conclu√≠do!</div>
                <div className="mt-1 text-sm text-gray-200">
                  Material gerado com sucesso. Voc√™ pode ir direto para Flashcards ou Resumo.
                </div>

                <div className="mt-4 flex flex-col sm:flex-row gap-2">
                  <button
                    type="button"
                    onClick={() => router.push("/flashcards")}
                    className="w-full sm:w-auto rounded-xl bg-[#00FF8B] text-[#0D0D0D] px-4 py-2 text-sm font-semibold hover:opacity-90"
                  >
                    Ir para Flashcards
                  </button>

                  <button
                    type="button"
                    onClick={() => router.push("/summary")}
                    className="w-full sm:w-auto rounded-xl border border-[#00FF8B]/35 bg-transparent text-[#00FF8B] px-4 py-2 text-sm font-semibold hover:bg-[#00FF8B]/10"
                  >
                    Ir para Resumo
                  </button>
                </div>

                {lastMaterialId ? (
                  <div className="mt-3 text-[11px] text-gray-400 break-all">
                    materialId: <span className="font-mono">{lastMaterialId}</span>
                  </div>
                ) : null}
              </div>
            </div>
          </div>
        ) : null}
      </div>
    </section>
  );
}
